.main-lable {
  font-family: Poppins-semibold;
}
/* The check-main-left */
.check-main-left {
  display: block;
  position: relative;
  cursor: pointer;
  margin-bottom: 0;
  font-size: 16px;
  padding-left: 1.5rem;
  font-family: 'Roboto-regular';
  color: var(--light-gray);
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.check-main-left input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 4px;
  left: 0;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  border: 1px solid var(--dark-gray);
  background-color: #ffffff;
  box-shadow: 0px 6px 25px #00000008;
  font-family: Poppins-medium;
  .brand-image {
    width: 120px;
    height: 90px;
  }
}

/* When the checkbox is checked, add a blue background */
.check-main-left input:checked ~ .checkmark {
  border: 1px solid var(--yellow);
  background-color: var(--yellow);
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.check-main-left input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.check-main-left .checkmark:after {
  left: 5px;
  top: 0;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}


/*File Upload*/
.profile-user-img{
  width: 115px;
  height: 115px;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
  border-radius: 100%;
}

.avatar-upload {
  position: relative;
  max-width: 205px;
  margin: auto;
  margin-bottom: 20px;

  .avatar-edit {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: transparent;
    border-radius: 100%;
    input {
      display: none;
      + label {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
      }
    }
    .edit-icon{
      position: absolute;
      right: 0;
      bottom: 5px;
      padding: 5px;
      background-color: var(--yellow);
      border-radius: 50%;
      img{
        width: 22px;
      }
    }
  }
}

.common-input{
  padding: 0 25px 0 25px !important;
}

.order-prod-image {
  width:100px;
  height:100px;
  object-fit: contain;
}

#my-orders {
  .card[aria-expanded="true"]{
    .card-header {
      background: #fff7dc !important;
      border-bottom-left-radius: 0px !important;
      border-bottom-right-radius: 0px !important;
    }
    &:nth-child(1){
      .card-header {
        background: #fff7dc !important;
        border-bottom-left-radius: 0px !important;
        border-bottom-right-radius: 0px !important;
      }
    }
  }
  .card[aria-expanded="false"]{
    .card-header {
      background: #FFFFFF !important;
    }
  }
}



