// suttort bot styles start
.support-bot-section {
  .bot-tab {
    font-size: 15px;
    border: 1px solid var(--yellow);
    border-radius: 40px;
    margin: 7px 0;
    padding: 5px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }

  .bot-message {
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    background-color: #f5fbff;
    display: block;
    text-align: left;
    padding: 12px 14px;
    width: calc(100% - 100px);
    margin: 10px 0;
    margin-right: 100px;
    @media screen and (max-width: 776px) {
      margin-right: 50px;
      width: calc(100% - 50px);
    }
    @media screen and (max-width: 576px) {
      margin-right: 20px;
      width: calc(100% - 20px);
    }
  }

  .bot-previous-option {
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    background-color: var(--light);
    display: flex;
    justify-content: flex-end;
    text-align: left;
    padding: 12px 14px;
    width: calc(100% - 100px);
    margin: 10px 0;
    margin-left: 100px;
    @media screen and (max-width: 776px) {
      margin-left: 50px;
      width: calc(100% - 50px);
    }
    @media screen and (max-width: 576px) {
      margin-left: 20px;
      width: calc(100% - 20px);
    }
  }

  .bot-button_group {
    display: flex;
    font-size: 15px;
    border: 1px solid var(--yellow);
    border-radius: 40px;
    margin: 7px 0;
    padding: 5px 20px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
}

.remove-image {
  display: flex;
  justify-content: center;
  align-items: center;
  top: 10px;
  right: -6px;
  background-color: white !important;
  height: 20px;
  width: 20px;
  border-radius: 50%;
}

// suttort bot styles end

