.custom-tooltip-main-card {
  position: relative;
  word-break: break-word;
  bottom: 15px;

  .tooltip-title {
    font-weight: bold;
    color: var(--white);
  }

  .tooltip-content {
    padding: 1rem;
  }

  .custom-tooltip {
    display: none;
    padding: 5px 15px;
    text-align: center;
  }

  .custom-tooltip.show {
    position: absolute;
    left: 0;
    right: 0;
    display: block;
    min-height: 30px;
    width: 100%;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 5px;
    z-index: 1;
    bottom: 42px;

    &:after {
      content: "";
      position: absolute;
      right: 27px;
      width: 0;
      height: 0;
      border-top: 10px solid transparent;
      border-left: 10px solid rgba(0, 0, 0, 0.8);
      border-bottom: 10px solid transparent;
      transform: rotate(90deg);
      bottom: -15px;
    }
  }
}

