.order-track {
  position: relative;

  .order-dot {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 5px;
      left: 0;
      height: 15px;
      width: 15px;
      border-radius: 50%;
      background-color: var(--white);
      border: 2px solid #E2E2E2;
    }

    &:after {
      content: '';
      position: absolute;
      top: 20px;
      left: 6px;
      min-height: 100%;
      width: 2px;
      background: #E2E2E2;
    }
  }
}

.order-track-active {
  position: relative;

  .order-dot {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 5px;
      left: 0;
      height: 15px;
      width: 15px;
      border-radius: 50%;
      background-color: #0AAF51;
      border: 2px solid #0AAF51;
    }

    &:after {
      content: '';
      position: absolute;
      top: 20px;
      left: 6px;
      min-height: 100%;
      width: 2px;
      background: #0AAF51;
    }
  }
}

.order-track-active:last-child{
  .order-dot{
    &:after {
      display: none;
    }
  }
}

.order-track-underprocess {
  position: relative;

  .order-dot {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 5px;
      left: 0;
      height: 15px;
      width: 15px;
      border-radius: 50%;
      background-color: var(--white);
      border: 2px solid #0AAF51;
    }

    &:after {
      content: '';
      position: absolute;
      top: 20px;
      left: 6px;
      min-height: 100%;
      width: 2px;
      background: #E2E2E2;
    }
  }
}

.order-track-underprocess:last-child{
  .order-dot{
    &:after {
      display: none;
    }
  }
}

.order-track:last-child {
  .order-dot {
    position: relative;

    &:after {
      display: none;
    }
  }
}

